.contactContainer{
    display: inline-flex; 
}

.contactHeader{
    text-align: left;
}

.contactLeft{
    padding: 20px 125px;
    text-align: left;
}

.contactRight{
    padding: 50px;
    justify-content:space-between
}

.contactData{
    padding-top: 50px;
}

input{
    margin: 20px;
}

#Name{
    height: 40px;
    width: 250px;
    font-size: 14px;
    border: 0px;
    opacity: 2;
}

#Email{
    height: 35px;
    width: 250px;
    font-size: 14px;
    border: 0px;
}

#Message{
    height: 150px;
    width: 550px;
    font-size: 14px;
    border: 0px;
}

#submitButton{
    
}

@media only screen and (max-width: 1024px) {
    .contactContainer{
        display:unset
    }

    #Message{
        height: 150px;
        width: 550px;
        font-size: 14px;
        border: 0px;
    }
  }


@media only screen and (max-width: 480px) {
    .contactContainer{
        display:unset
    }

    #Message{
        height: 300px;
        width: 250px;
        font-size: 14px;
        border: 0px;
    }
}